import { pinJSONToIPFS } from "utils/store/store_pinata.js";
import { contractAddress, gatewayAddress } from "types";
require("dotenv").config();
const contractABI = require("../../contract-abi.json");
const Web3 = require('web3');
const web3 = new Web3('https://api.baobab.klaytn.net:8651/');



async function loadContract() {
  return new web3.eth.Contract(contractABI, contractAddress);
}

export const mintNFT = async (coupon, address) => {
  if (address.trim() == "") {
    return {
      success: false,
      status: "‚ùóPlease make sure all fields are completed before minting.",
    };
  }


  // make metadata
  const metadata = new Object();
  metadata.couponName = coupon.metadata.name;
  metadata.couponUnit = coupon.metadata.keyvalues.couponUnit;
  metadata.couponDescription = coupon.metadata.keyvalues.couponDescription;
  metadata.image = `${gatewayAddress}${coupon.ipfs_pin_hash}`;

  const pinataResponse = await pinJSONToIPFS(metadata);
  if (!pinataResponse.success) {
    return {
      success: false,
      status: "üò¢ Something went wrong while uploading your tokenURI.",
    };
  }
  const tokenURI = pinataResponse.pinataUrl;
  const rand = Math.random().toString(36)

  window.contract = await new web3.eth.Contract(contractABI, contractAddress);

  const transactionParameters = {
    to: contractAddress, // Required except during contract publications.
    from: window.ethereum.selectedAddress, // must match user's active address.
    data: window.contract.methods
      .issueCoupon(address, rand, tokenURI)
      .encodeABI(),
  };

  try {
    const txHash = await window.ethereum.request({
      method: "eth_sendTransaction",
      params: [transactionParameters],
    });
    return {
      success: true,
      rand: rand,
      status:
        "‚úÖ Check out your transaction on Etherscan: https://baobab.scope.klaytn.com/tx/" +
        txHash,
    };
  } catch (error) {
    return {
      success: false,
      status: "üò• Something went wrong: " + error.message,
    };
  }
};
